
.projectCard {
  width: calc(100vw - 6rem); /* Calcula a largura para caber perfeitamente */
  max-width: 300px; /* Um limite máximo */
  height: 220px;
}

.projectCard:hover {
    border-color: rgba(0, 255, 127, 0.5);
    box-shadow: 0 0 15px rgba(0, 255, 127, 0.2);
}

.folder {
  transform: scale(1.5);
}

/* Sobrescreve a cor de fundo dos papéis para combinar com o tema escuro.
   Usamos :global() para atingir a classe interna do componente Folder. */
.folder :global(.paper) {
    background-color: #1a1a1a !important;
    border: 1px solid #333;
}

.folderLabel {
    margin-top: 1rem;
    color: #00FF7F;
    text-shadow: 0 0 5px #00FF7F;
    font-weight: 600;
}

.projectDetails {
    position: absolute;
    bottom: -10px; /* Posiciona abaixo da pasta quando aberta */
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;
    width: 300px;
}

.spotlightCard {
    background-color: #1a1a1a;
    border: 1px solid #00FF7F;
    color: white;
    text-align: center;
    --spotlight-color: rgba(0, 255, 127, 0.1);
}

.spotlightCard h3 {
    color: #00FF7F;
    margin-top: 0;
}

.spotlightCard h3 a {
    color: inherit; /* Herda a cor verde do h3 */
    text-decoration: none;
    transition: text-shadow 0.3s;
}

.spotlightCard h3 a:hover {
    text-shadow: 0 0 8px #00FF7F;
}

.projectDescription {
    color: #ccc; /* Cor cinza claro para a descrição */
    font-size: 0.9rem;
}

.projectLink {
    display: inline-block;
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    background-color: transparent;
    border: 1px solid #00FF7F;
    color: #00FF7F;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s, color 0.3s;
}

.projectLink:hover {
    background-color: #00FF7F;
    color: black;
}

/* Estilo para a imagem do projeto dentro do papel */
.projectImage {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Garante que a imagem cubra o papel sem distorcer */
    border-radius: 2px; /* Pequeno arredondamento para combinar com o papel */
    mix-blend-mode: normal; /* Garante que a imagem não seja afetada por blend modes globais */
    cursor: pointer; /* Indica que a imagem é clicável */
}

/* --- Media Queries para Responsividade --- */

@media (max-width: 768px) {
    .projectCard {
        
        max-width: 320px; /* Evita que o card fique muito grande em tablets */
    }

    .projectDetails {
        width: 95%;
        max-width: 320px;
    }
}

@media (max-width: 480px) {
  .projectCard {
  /* --- AQUI ESTÁ A MUDANÇA PRINCIPAL --- */
  width: 75vw;       /* O card vai ocupar 75% da largura da tela do celular */
  max-width: 280px;  /* Definimos um limite para não ficar grande demais */
  
  height: 220px;
}

  .folder {
    transform: scale(1.2); /* Diminui um pouco o tamanho da pasta */
  }

  .folderLabel {
    font-size: 0.9rem; /* Diminui o texto abaixo da pasta */
  }
}

